.App {
  font-family: sans-serif;
  text-align: center;
}

.card {
  width: 350px;
  height: 500px;
  margin: 0 auto;
  background-color: #ddd;
  box-shadow: 0 0 0 0.5 #ddd;
  border-radius: 20px;
}
.card-header {
  padding: 10px 10px;
  border-bottom: 1px solid #969699;
}
.formTodo {
  margin-top: 10px;
  margin-left: 0px;
}
.formTodo input {
  padding: 10px;
}
.add-todo {
  margin-left: 10px;
  font-size: 20px;
}
.formTodo ul {
  padding-block-end: 0 !important;
  padding-block-start: 0 !important;

  margin-top: 20px;
}
